export function Theme(){
  let v="col-sm-12 col-md-4 gencols";

  return (
      <div class="row">
        <ArtGuild />
        <div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red",fontSize:"30px"}}>A screenshot in one of the campaign groups</div>
        <ThemeWrap img={t40}headline="'False Information cited in the screenshot above'" date="Thursday 28th July, 2022" msg="On Thursday morning the 28th of July, 2022, one who calls him/herself the Polical Witch was cited sharing a message about Dangote with claims that Dangote had stepped down to back Muyinda Carlton who is also a presidencial guild aspirant for Makerere University. Basing on the immediate contacts made to Alionzi Lawrence a.k.a Dangote, he says that that is false information and asked that the claim be ignored. Basing on the past election activities of Makerere University, such unauthentic ways of campaigning have been cited taking place in various campus social media platforms which leaves media teams that render such tactics with lost trust and respect for their media teams"/> 
        <ThemeWrap img={t41}headline="Reference to Paragraph 9" date="Thursday 28th July, 2022" msg="In the recent letter above, in paragraph 9, Alionzi Lawrence calls for avoiding the blame games, pointing fingers and accusing one another. Mayambala Derrick, Alionzi's campaign manager on hearing about the incident replied with the following statement; 'We operate on an ideological school of thought which engages inclusiveness, unity, transparency and activism only subscribing to those who can think with us and believe in leadership for a common student: with our principles subjective and objective to anyone's opinions, I believe he's about to join our team anytime because we believe he's embracing our campaign however, WE ARE STILL IN THE CAMPAIGN NOT FOR US INDIVIDUALLY BUT BECAUSE IT'S WHAT MAKEREREANS WOULD WISH FOR AND DESERVE'"/> 
        
       
        
        
        <div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red",fontSize:"30px"}}>The Final Rally</div>
    <ThemeWrap img={t33}headline="Today at 3pm" date="Thursday 14th July, 2022" msg="Alionzi Lawrence once again gathered his supporters who turned up in extremely larger numbers compared to the previous gathering. The intension was to lead his supporters to the Makerere Rugby grounds where all other aspiring presidential candidates including Alionzi will have a final rally prior to the voting that is to happen tommorrow. Alionzi Lawrence after entertaining students finally leads them to the grounds at around 5:40pm"/>
    <ThemeWrap img={t36}headline="Part of Alionzi's supporters" date="Thursday 14th July, 2022" msg="A portion of Dangote's supporters on arrival at the rally grounds spread out all over the pitch dancing, singing and raising posters of Alionzi Lawrence."/>   
    <ThemeWrap img={t34}headline="Isaac" date="Thursday 14th July, 2022" msg="Right behind me is another portion of the pavillion that accomodated students who preffered to be seated. Fellow students, to the few I have interracted with, I realized you actually took support personally to the party basis. Please support the candidate, not the party. The party is there to facilitate activities. We are not going to be split because of some of you who brain feed others with activities that political parties have done in the past. Let's look at what the candidate can do and not what you think the party is going to tell the candidate to do."/> 
    <ThemeWrap img={t35}headline="Rogers" date="Thursday 14th July, 2022" msg="'Let's send him to represent. He has the English, his really promising. He is defensive. I guess the debate panelists know him for this. His not a joke.', that was a statement provided by one of his OBs, Rogers. Immediately after Alionzi had finished his presentation, he walks off the stage to leave room for other candidates. "/> 
   <div><div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red",fontSize:"30px"}}>The Sad News</div><div style={{textAlign:"center",fontFamily:"charm",color:"grey",paddingBottom:"15px",fontSize:"20px"}}>As Alionzi led majority of his supporters out of the grounds for his opponents to present, a group of FDC students were seen throwing bottles and stones towards the crowd that was waiting to listen to other presentations from other candidates. It was really a shame that their act was a cost to their own FDC candidate as well who failed to present because of their own notorious acts. This is an advise to all political party leaders to always advise their flag bearers on issues concerning self respect. </div></div> 
   <ThemeWrap img={t37}headline="Suspension of Guild Elections" date="Thursday 14th July, 2022" msg=""/> 
   <ThemeWrap img={t38}headline="" date="Thursday 14th July, 2022" msg="Basing on the violence acts that transpired which resulted to a death case, Makerere University decided to suspend the Guild elections indefinitely. "/> 


   
  
        
        
         <div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red",fontSize:"30px"}}>13th July's Bash at Nsibirwa/Nkuruma Quadrangle at 2pm</div>
        <ThemeWrap img={t27}headline="Mega Bash" date="Wednesday 13th July, 2022" msg="Warm welcome to you all as Alionzi Lawrence invites you for a Bash today 13th July, 2022 starting at 2pm in Nsibirwa/Nkuruma quadrangle. Come relax your mind, come and confirm to allow him allow you to allow him to handle. Please share the message to all family members (Makerere University Students). Thank you. "/>
        <ThemeWrap img={t28}headline="Part of the crowd" date="Wednesday 13th July, 2022" msg=""/>
               <ThemeWrap img={t29}headline="Another part of the crowd" date="Wednesday 13th July, 2022" msg="Alionzi Lawrence today has been seen being the candidate who has attracted the highest crowd numbers compared to the rest of the candidates in the same race. This still leaves Alionzi Lawrence with the highest chances to come in as the next Guild president of Makerere University because it is evidentially seen basing on the numbers that turn up to calls."/>
        <ThemeWrap img={t30}headline="Isaac, Mark, Rogers, Amos (Alionzi'z OBs)" date="Wednesday 13th July, 2022" msg="A group of Alionzi's OBs from the same Secondary School were seen standing together cheering their OB. Alionzi then leads his people outside campus for another walk around campus. The crowd was not happy with the police's decision to hold back the truck that carried the public address system but the candidate proceeded despite his system being captured from Makerere western gate."/>
        <ThemeWrap img={t31}headline="Along Douglas Villa Street" date="Wednesday 13th July, 2022" msg="DouglasVilla hostel residents just kept on dancing to Alionzi's 'eeh Dangote' song and showed smiles of happiness and gratefulness for the candidate. Very many students seem to wish him all the best of good luck"/>
        <ThemeWrap img={t32}headline="Alionzi's crowd fully floods two-way streets" date="Wednesday 13th July, 2022" msg="It was really shocking when all traffic was paralized for about half an hour just to wait for the complete crowd to disperse."/>
        <div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red",fontSize:"30px"}}>On The Launching of The Manifesto</div>
        <ThemeWrap img={t19}headline="Arrival of Members" date="Sunday 10th July, 2022" msg="On Sunday 10th July, 2022 at around 4pm students and well wishers started to converge at the Guild canteen to witness the launching of Alionzi Lawrence a.k.a Dangote's manifesto. The turn up was high since many and many kept on arriving even amidst the speeches that had kicked off  "/>
        <ThemeWrap img={t22}headline="Presence of Natseli, Price, Calvin Mugambwa and Katushabe Kenneth" date="Sunday 10th July, 2022" msg="On the high table, the presence Natseli Godfrey, Bhosa Price, Mugambwa Calvin and Katushabe kenneth who where equally rallying for the NUP ticked was witnessed and it was one of the key notes that was highly respected and appreciated about the NUP party."/>
        <ThemeWrap img={t20}headline="Former 85th Guild President-Katerega Julius speaks" date="Sunday 10th July, 2022" msg="In his speech, he highly respected the presence of the other candidates who rallied for the same NUP ticket. He respected the decision that all the candidates decided to team up and push for their comrade, Alionzi Lawrence, which he said to be one of the strongest decisions that NUP candidates usually expose. He said that all this occurs just because the NUP party is a party of evidential unity."/>
        <ThemeWrap img={t21}headline="Former 86th Guild President-Ssempijja Ivan speaks" date="Sunday 10th July, 2022" msg="Ivan in his speech was also very thankful for the unity and said that Makerere leadership should not be taken for self service but rather student service. In his speech, a high level of dead silence was noticed and a huge loud of applause immediately after his speech. He has been seen together with Alionzi Lawrence during the rallies and a few meetings and students believe that the best of the best will emerge."/>
        
        <ThemeWrap img={t23}headline="(L-R) Calvin Mugambwa, Natseli Godgrey, Alionzi Lawrence, Bhosa Price, Katushabe Kenneth" date="Sunday 10th July, 2022" msg="The four candidates that had rallied for the same NUP ticket are evidencially present for Alionzi Lawrence in his race. Mugambwa said in his speech that during the rallies ealier he was a friend to Alionzi lawrence, he assured that Makerere is in good hands with Dangote and requested that all his supporters offer the support to Alionzi Lawrence. Bhosa Price in his speech spoke with pain saying that nothing stops them no matter the suspension. He emphasized that they are not ready to sit on a table for some one who has already made decisions and promised to be there for Alionzi Lawrence till the final day of voting."/>
        <ThemeWrap img={t24}headline="What happened immediately after primaries" date="Sunday 10th July, 2022" msg="Natseli shared to the crowd that he immediately acquired hypertension for a while after losing the the NUP ticket to Alionzi Larence. He said that he came to add up to Alionzi because he believes in the party and that what the party has chosen is for all of us. He later on requested the members to allow to allow him endorse Alionzi Lawrence. Alionzi Lawrence in his speech also highly showed respect for Natseli."/>
        <ThemeWrap img={t26}headline="" date="Sunday 10th July, 2022" msg=""/>
        <ThemeWrap img={t25}headline="Alionzi's Speech During His Manifesto Launch" date="Sunday 10th July, 2022" msg="In my tribe, we say that; 'When two brothers fight to the death, a stranger inherits their property.'
I am not saying this the for sake of votes, but to acknowledge a good fight that was exhibited by Natseli, MCM, and Katushabe. 

I, therefore, request to be the heir of the faith you had in them and accomplish their fight. 
Like great empires that are founded on bloodshed and tombs, my candidature is founded on 4 wounded soldiers; Bbosa Price, Roy Rugumayo, Mwegiwa Calvin, and Katushabe Kenneth. I am because of them, the junta manufactured a tyranny witch hunter inadvertently, therefore I will not relent until the dawn of injustices arrives at the hill.

Liberty and academic injustices have given birth to a war child, a university is not a university when it suffocates rights of expression, and if our rights are in captivity, it's an uprising against the invasion and its devices.

I call upon all the Makerereans to time travel and restore the past glory of our institution, an institution that had powers and rights in condemning what is wrong. That is what we stand for.
Arise soldiers for better welfare and better policies at our Hill.
Because I stand for the truth, when I stand alone, I can see far. 

But when I stand on your shoulders I can see far because beneath your souls lie giants.
Wise men say that standing for the truth is standing alone, but when you stand on fertile grounds, you are not alone, because you have revolutionary crops aside. This university has ripe crops which can join me to stand against any form of invasion.

Allow, Us Mak. Dangote Will Handle."/>
       
        
        <div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red",fontSize:"30px"}}>Alionzi Lawrence's Trek around Makerere University</div>
      <ThemeWrap img={t10} headline="Alionzi Lawrence a.k.a Dangote Treks" date="Friday 8th July, 2022" msg="A Friday 8th July, 2022 as the NUP flag bearer Alionzi Lawrence sets off for a trek with his 'family'. The candidate had overwhelming numbers of students who unitely joined the 'family' trek. Evidence the photos that follow below." />
      <ThemeWrap img={t7} headline="How big is that crowd?" date="Friday 8th July, 2022" msg="The red crossing over the top of the students was an estimate of the population size that showed higher chances and evidence that Alionzi Lawrence deserves to head the 'family'. Ofcourse a sample of physical evidence is prevailing leaving the whole university expectant for his success."/>
      <ThemeWrap img={t12} headline="'Makerere has to Appreciate'"date="Friday 8th July, 2022" msg="One of the crowd members was heard saying that indeed Makerere has to appreciate. What could be the reasons as to why such a comment comes up?"/>
      <ThemeWrap img={t9} headline="Alionzi walks the family outside campus premises"date="Friday 8th July, 2022" msg="Alionzi Lawrence finally after a family tour inside the campus premises decides to extend his trek outside campus boundaries where he said another majority of 'family' members reside."/>
      <ThemeWrap img={t17} headline="Extremely Unbeliveable Crowd"date="Friday 8th July, 2022" msg="This was the only outstanding thing seen that left every heart overwhelmed. The jubilations from the crowd left no opportunity for the sound system to be heard which was an awesome experionce. Majority of the students seem to be willing to have 'Dangote' as their leader."/>
      <ThemeWrap img={t18} headline="Take note of the crowd size"date="Friday 8th July, 2022" msg="Believe as you see it."/>
      <ThemeWrap img={t11} headline="Brief as it is, 'Allow Us Mak'"date="Friday 8th July, 2022" msg=""/>
      <ThemeWrap img={t13} headline="Appreciation to Kayas Makerere University" date="Friday 8th July, 2022" msg="One of the reasons as to why I ususally show a smile is because of one statement that went, 'Go for student service but not self service'. My sincere appreciation to Isaac Opio, Kahuma Charles and the rest of the team for actually confirming my plans for Makerere University students and I will still promise you that I am not coming in for self service but for student service."/>
      <ThemeWrap img={t14}headline="" date="Friday 8th July, 2022" msg=""/>
      <ThemeWrap img={t15} headline=""date="Friday 8th July, 2022" msg=""/>
      <ThemeWrap img={t16} headline="Douglas Villa Street"date="Friday 8th July, 2022" msg=""/>
      <ThemeWrap img={t8} headline="J.J hostel parking yard"date="Friday 8th July, 2022" msg="Alionzi lawrence walked his family till the night hour maintaining his originally big crowd across the area of Kikoni. There are signs of great expectations for Alionzi Lawrence as Makerere's Guild President"/>


    <div class={v}><img src={t1} class=" d-block w-100" alt="..."  /><div><div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red"}}>Makerere has to Appreciate</div><div style={{textAlign:"center",padding:"2px",fontFamily:"charm",color:"grey"}}>I have began my journies as I always promised. But with everything I do, I always remeber that God guides and provides. My dear Makerereans, I am not here for leadership but for your service I promise </div></div><hr></hr></div>
    <div class={v}><img src={t2} class=" d-block w-100" alt="..."  /><div><div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red"}}>Dangote gathering his team to his 1st rally at CHS</div><div style={{textAlign:"center",padding:"2px",fontFamily:"charm",color:"grey"}}>Support has been evidenced literally just before Dangote moved out of his room where a massive crowd had gathered to see their candidate move out to head for the rally. Elections are headed for the mid of this month. </div></div><hr></hr></div>
    <div class={v}><img src={t3} class=" d-block w-100" alt="..."  /><div><div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red"}}>The Massive crowd</div><div style={{textAlign:"center",padding:"2px",fontFamily:"charm",color:"grey"}}>Alionzi Lawrence a.ka. Dangote leading the longest crowd towards the rally grounds. The candidate is determined and has won the majority of the students willingness to vote for him leaving alot of expectations for his success. He left the whole debate house in suspence earlier on  because of his brilliant  defencive answers. </div></div><hr></hr></div>
  <div class={v}><img src={t4} class=" d-block w-100" alt="..."  /><div><div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red"}}>The slogan "Allow Us Mak"</div><div style={{textAlign:"center",padding:"2px",fontFamily:"charm",color:"grey"}}>Allow us Mak was  the message of the journey to the rally grounds. During his journey, Dangote  requested for original unity by clicking the green button besides him at the top. "Let's unite, through the button in order to stay together and prove our unity," he said. </div></div><hr></hr></div>
    <div class={v}><img src={t6} class=" d-block w-100" alt="..."  /><div><div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red"}}>The "Eh Dangote Dance"</div><div style={{textAlign:"center",padding:"2px",fontFamily:"charm",color:"grey"}}>Dangote paused the crowd to dance to his "Eh Dangote" Song. In his song, he asked to be "allowed" and the rotational dance reply  by the whole crowd to his request implied being granted the request genuinely. </div></div><hr></hr></div>
    <div class={v}><img src={t5} class=" d-block w-100" alt="..."  /><div><div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red"}}>The Respect that was observed</div><div style={{textAlign:"center",padding:"2px",fontFamily:"charm",color:"grey"}}>Alionzi Lawrence a.k.a Dangote's crowd was noticed to be non-violent, calm and missioned to air out the message peacefully. The young influencial leader again promised not to forget fellow students during his leadership period. </div></div><hr></hr></div> 
      
     </div>
  );
}
 
export function ThemeWrap(props){
  let v="col-sm-12 col-md-4 gencols";
return(<div class={v}><img src={props.img} class=" d-block w-100" alt="..."  /><div>  <div><div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red"}}>{props.headline}</div><div style={{textAlign:"center",paddingBottom:"5px"}}><span style={{border:"1px solid grey",padding:"3px",borderRadius:"10px",color:"grey",fontFamily:"charm",fontSize:"11px"}}>{props.date}</span></div></div><div style={{textAlign:"center",padding:"2px",fontFamily:"charm",color:"grey"}}>{props.msg}</div></div><hr></hr></div>)

}

