function Art(){
  let data=""
  const [comNumb,setNumb]=useState('')
  const [dis,setDis]=useState('')

  useEffect(()=>{
   
  fetch('/collection_comments_comments').then(res=>res.json()).then(res=>{
    setNumb(res.length)
res.forEach(comment=>{

  data+='<div>'+comment.contact+"</div><div>"+comment.body+"</div><hr></hr>"

})

setDis(data)
  })
   
  })
  
 

  
  return (
    
     
      <div style={{background:"white"}}class="row">
     
<p></p> 
<div style={{textAlign:"center",fontWeight:"bold",color:"red"}}> {comNumb} comments below:</div>
<div style={{textAlign:"center",padding:"20px",fontSize:"15px",fontFamily:"charm"}}><div dangerouslySetInnerHTML={{__html:dis}}/></div>
  <Alert2/>
     </div>
  );  
}





