  export function NuWhatsAppGroupLinks(){
   
    const [whatsAppGroupLinks,setWhatsAppGroupLinks]=useState('')
    useEffect(()=>{
 
      let data=""
        fetch('/collection_whatsappgrouplinks_links').then(res=>res.json()).then(res=>{
         res.reverse()
          res.forEach(groupLink=>{
         if(groupLink.campusId==2){
          data+='<div style="font-size:15px;"><div style="font-size:20px;">'+groupLink.groupName+'</div><div>'+groupLink.campus+'</div><div>Group Admin: '+groupLink.groupAdmin+'</div><div style="font-family:charm;">'+groupLink.description+"</div><div style='font-size:20px;'><a  href="+groupLink.link+"><div class='hovereffect' style='color:green;'><span class='fa fa-whatsapp hovereffect'></span> <span class='hovereffect'>Join group</span></div></a></div></div><hr></hr>"
         }
else{
  ;
}
       
        
          })
          setWhatsAppGroupLinks(data)
            })
      
           
      
        
        },[])
  
    let v=""

    return(
  
 <div>
  <NuNav/>

   <div style={{padding:"5px"}}>
 <div style={{color:"red",textAlign:"center", fontSize:"25px"}}>WhatsApp Groups</div><p></p>
 <div class="row"><div class={v}></div><div class={v} dangerouslySetInnerHTML={{__html:whatsAppGroupLinks}}/><div class={v}></div></div>
    </div>
 </div>
  
    )
  }
  
export default NuHome