export function CarouseleCampus(){

  let caroushght="400px";
  return (
       <div id="carouselExampleControls" class="carousel slide carousel-slide col-md-12" data-bs-ride="carousel">
       <div class="nega"><strong></strong></div>
      <div class="carousel-inner">
      <div className="row"> 
         <div className="col-1"></div>
         <div class="col-10">
      <div class="carousel-item active">
<div class="carousel-caption caption-four  d-md-block">
<br></br><br></br><br></br><br></br><br></br><br></br><div><div class="carmsgcolor"></div></div>

 </div>
   </div>
  
   
  <CarousItem hght={caroushght} img={first} msg="I really want to appreciate Kayas for allowing me share the opportunity to you as well" />
  
  
  </div>
     <div className="col-1"></div>
     </div>
  
 
  
  

   </div>
    <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleControls"  data-bs-slide="prev">
    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
    <span class="visually-hidden">Previous</span>
  </button>
  <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleControls"  data-bs-slide="next">
    <span class="carousel-control-next-icon" aria-hidden="true"></span>
    <span class="visually-hidden">Next</span>
  </button>
  </div>
  );
}


function Art(){
  let data=""
 
  const [dis,setDis]=useState('')
  const [comNumb,setNumb]=useState('')

  useEffect(()=>{
   

  fetch('/collection_campus_comments').then(res=>res.json()).then(res=>{
    setNumb(res.length)
    res.forEach(comment=>{
   
      data+='<div>'+comment.name+" "+comment.contact+"</div><div>"+comment.body+"</div><hr></hr>"
    
    })
    
    setDis(data);
      })
  
  },[])




  

  
  return (
    
     
      <div style={{background:"white"}}class="row">
         <div style={{padding:"15px"}}>  <div style={{fontSize:"20px",color:"red",paddingTop:"10px",textAlign:"center"}}>Top Stories Taking Place</div> </div>
       <div style={{padding:"20px"}}>

       <Anchor href="/pages/muk/muktopstories"  heading1="MAKERERE TOP STORIES" heading2="Get trending stories taking place in around Makerere as we provide them to you." caption="Tap here to see trending stories in Makerere..." />
       <Anchor href="/pages/nu/nutopstories"  heading1="NDEJJE UNIVERSITY STORIES" heading2="Get trending stories taking place in Ndejje University as we provide them to you." caption="Tap here to see trending stories in Ndejje..." />
       <Anchor href="/pages/kyu/kyutopstories"  heading1="KYAMBOGO UNIVERSITY STORIES" heading2="Get trending stories taking place in Kyambogo University as we provide them to you." caption="Tap here to see trending stories in Kyambogo..." />
       <Anchor href="/pages/memeopinions"  heading1="FUN, MEMES AND LAUGHTERS" heading2="Have a look at memes and commedy stories and also share them through your whatsapp using the WhatsApp share button found at the bottom of each story." caption="Tap here to see and read detailed memes from Kayas..." />

       </div>
       <div style={{textAlign:"center"}}>  <div style={{fontWeight:"bold",fontSize:"20px",color:"red",paddingTop:"10px"}}>What's at Campus? </div> </div>
     
      
       <p></p><div style={{textAlign:"center"}}>  <div style={{fontWeight:"",color:"grey",padding:"4px"}}>Makerere University celebrates 100 years. What you have liked about Makerere for the short time you have been in Makerere University <p></p>Add your comment through the Form found just below this poster</div> <p></p></div>
    <Theme/>
     <div style={{textAlign:"center",padding:"20px"}}> 
    
      <form method="post" action="/collection_campus_comment">
  <div class="mb-3">
 

  <input type="text" class="form-control" autoComplete="off" name="name" placeholder='Your Name e.g. Charles'   required></input>
<br></br>
  <input type="text" class="form-control" autoComplete="off" name="tel" placeholder='Enter Your WhatsApp Contact'  minLength={10} maxLength={10} required></input>
<br></br>

    <textarea rows="5"  type="text" class="form-control" autoComplete="off" name="msg" placeholder='Type Your Message Here' required></textarea>
    <br></br>
    <input type="text" class="form-control" autoComplete="off" name="pin" placeholder='Enter Your PIN' minLength={5} maxLength={5} required></input>
  </div>

  
  <button type="submit" class="btn btn-success">Add Your Post</button><p></p>
  <a href="https://kayas-mak.herokuapp.com/pages/services" ><label style={{color:"green"}}><span class="hovereffect"><span class="fa fa-user"></span> REGISTER</span></label></a>
</form>
      
      
</div>

<div style={{textAlign:"center",fontWeight:"bold",color:"red"}}>{comNumb} Posts below:</div>
<div style={{textAlign:"center",padding:"20px",fontSize:"15px",fontFamily:"charm"}}><div dangerouslySetInnerHTML={{__html:dis}}/></div>
  
     </div>
  );  
}





export function ArtCompRead(props){
  let v="col-12 col-sm-12 col-md-6",phone=256703852178,link="https://api.whatsapp.com/send?phone="+props.phone+"&text=Hello%20"+props.name+",%20my%20comment%20about%20your%20article%20on%20Kayas%20Makerere%20University%20is;%20"
  return (

<div class={v} style={{paddingBottom:"10px"}}><p></p> <div style={{padding:"10px"}}><div className="row"> <div style={{textAlign:"center",paddingTop:"30px"}}class="col-6"><span style={{fontWeight:"bold",fontSize:"15px", color:"green"}}>{props.name}</span> <span style={{fontWeight:"",textAlign:"center",fontSize:"15px", color:"rgb(187, 9, 9)"}}>{props.post}</span>  <div> <a href={link}><button type="button" class="btn btn-sm btn1 btn-success"> <span class="fa fa-whatsapp"> </span> Your Comment?</button></a></div> </div> </div></div> <div style={{paddingLeft:"5px"}}>    <div  style={{color:"grey",textAlign:"center",fontFamily:"Charm",border:"0.5px solid grey",borderRadius:"20px",padding:"3px",overflow:"scroll"}}>{props.msg}</div>    </div></div>

  );
}




export function Theme(){
  let v="col-sm-12 col-md-4 gencols";
  
  return (
      <div class="row">
      <div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red"}}></div>
        <ThemeWrap img={camp1}headline="" date="Kayas Makerere" msg=""/> 
      
     </div>
  );
}





export default Campus