      export function AllArticles(){
let data=""
const[articles,setArticles]=useState('')
useEffect(()=>{
fetch('/getAllArticles').then(res=>res.json()).then(resp=>{
    
    resp.forEach(articleObject=>{
        data+=`<div class='col-sm-6 col-md-4'><div><a  style='color:black;' href='/pages/pubarticles/article/${articleObject.id}'><span class="hoverEffectUnderline"><div  style='padding-bottom:10px;'><div style='color:green;font-size:18px;font-weight:bold;'>${articleObject.headline1}</div><div style='font-size:12px;color:red;'>${articleObject.institution} student wrote this. Tap to read details.</div><div style='font-size:12px;color:grey;'>Article ${articleObject.id}, written by: ${articleObject.author} ${articleObject.contact}</div><div style='color:grey;'><span style='color:red;'>${articleObject.pubArticleOpinions.length}</span> students have commented</div></div> </span></a></div><div><a href="https://chat.whatsapp.com/BU6aMsNR6jL5x11rcWc9HZ"><div class='btn btn-success btn-sm hovereffect'><span class='fa fa-whatsapp'></span> Join Kayas group</div></a></div><hr></hr></div>`
    })
    setArticles(data)
})

},[])


        return(<div>
<div style={{color:"red",fontSize:"25px",textAlign:"center",padding:"5px"}}>All Articles</div>
<ArticlesNav/>
  <div style={{padding:"3px"}}class="row" dangerouslySetInnerHTML={{__html:articles}}/>
    
        </div>)
      }

      export function MyArticles(){
        const[status,setStatus]=useState('')
        let data=""
        const[myArticles,setMyArticles]=useState('')
       
                return(<div>
      <ArticlesNav/>
          <div style={{padding:"20px"}}><div style={{color:"green", fontSize:"24px",fontWeight:"bold"}}>SEE YOUR ARTICLES</div>
          
                   
        <form method="post" id="myArticlesForm" action="#">
     <div class="mb-3">
    
    <input type="text" class="form-control" autoComplete="off" name="contact" placeholder='Enter WhatsApp Contact e.g 0703852178'></input>
  
   
   <br></br>
     <input type="text" class="form-control" autoComplete="off" name="pin"  placeholder="Enter your PIN e.g. 12345 (5 digits)" ></input>
    
   
     </div>
     
     <div style={{fontSize:"17px",paddingBottom:"10px"}} dangerouslySetInnerHTML={{__html:status}}/>
     <div style={{borderRadius:"18px"}}  onClick={
       ()=>{
    
         if(Array.from(document.getElementById("myArticlesForm")[0].value).length<10||Array.from(document.getElementById("myArticlesForm")[0].value).length>10){
setStatus("<div style='color:red'>Please enter a correct number......</div>")

}else if(Array.from(document.getElementById("myArticlesForm")[1].value).length<5||Array.from(document.getElementById("myArticlesForm")[1].value).length>5)
         {
            setStatus("<div style='color:red;'>Please enter a PIN of 5 digits ......</div>")
         }

else{
    setStatus("<div style='color:green;'>Getting your Articles, <span style='color:red;'>please wait ......</span></div>") 
    fetch('/verifyUser',{
        method:"post",
        headers:{'Content-type':'application/json'},
        body:JSON.stringify({
contact:document.getElementById("myArticlesForm")[0].value,
pin:document.getElementById("myArticlesForm")[1].value,
        }) 
    }).then(res=>res.json()).then((resp)=>{
       
        
        if(resp.registered==false){
            setStatus("<div style='color:red;'>You are not registered with Kayas, please register with Kayas then begin writing articles</div>")
        }
         else{
            if(resp.registered==true&&resp.pin==false){
                setStatus(`<div style='color:green;'><span style='color:red;'>${resp.details.name},</span> your <span style='color:red;'>PIN is incorrect,</span> please try again......</div>`)
    
            }else{
                setStatus(`<div style='color:green;'><span style='color:red;'>every thing is okay.</div>`)
                  
         
       setStatus(`<div style='color:green;'><span style='color:red;'>${resp.details.name},</span> please be patient as we finalize with getting your articles ......</div>`)

       fetch('/getMyArticles',{
        method:"post",
        headers:{'Content-type':'application/json'},
        body:JSON.stringify({
          contact:document.getElementById("myArticlesForm")[0].value,
        })
    }).then(resp=>{
        setStatus(`<div style='color:green;'>Your Articles have been received, please wait we share with you ......</div>`)

        return resp.json()}).then(resp=>{
if(resp.length==0){
  setStatus(`<div style='color:red;'>You don't have any Articles written. Please write some and get back. Thank you.</div>`) 

}else{
resp.reverse()
  resp.forEach(articleObject=>{

    let newComments;
if(articleObject.newCommentsNumb==undefined){
  newComments=0
}else{
  newComments=articleObject.newCommentsNumb
}

    data+=`<div class='col-sm-6 col-md-4' onclick=fetch('/resetPubArticlesNewCommentsNumb',{method:"post",headers:{"Content-type":"application/json"},body:JSON.stringify({id:${articleObject.id}})}) ><div><a  style='color:black;' href='/pages/pubarticles/article/${articleObject.id}'><span class="hoverEffectUnderline"><div  style='padding-bottom:10px;'><div style='color:green;font-size:18px;font-weight:bold;'>${articleObject.headline1}</div><div style='font-size:12px;color:red;'>${articleObject.institution} student wrote this. Tap to read details.</div><div style='font-size:12px;color:grey;'>Article ${articleObject.id}, written by: ${articleObject.author} ${articleObject.contact}</div><div style='color:grey;'><span style='color:red;'>${newComments}</span> new comments | <span style='color:red;'>${articleObject.pubArticleOpinions.length}</span> comments</div></div> </span></a></div><div> <a href='/pages/pubarticles/editarticle/${articleObject.id}'> <button  class='btn btn-success btn-xs'>Edit this Article</button></a></div> <hr></hr></div>`
})
setMyArticles(data)
    setStatus(`<div style='color:green;'>Done, scroll down to see your Articles.</div>`) 

}

          
         
          })
       
            }
            
          
        }
       
    }
        

    )
    
  
}
       }
     } class="btn btn-success hovereffect">Get your Articles</div><p></p>
    
     </form>
     <div style={{padding:"3px"}}class="row" dangerouslySetInnerHTML={{__html:myArticles}}/>
     <hr></hr>
     </div>
            
       
        
                </div>)
              
      }