export function Ent2(){
  const[val,setVal]=useState(0)
  const[msg,setMsg]=useState('')
  function Infor(){
    switch(val){
      case 1:setMsg("ones")
      break;
      case 2:setMsg("two")
      break;
      case 3:setMsg("three")
      break;
      default:setMsg("End of story.......")
    }
  }
  return(<div>
    {val}
    <div>{msg}</div>
    <div style={{paddingTop:"10px"}} ><div class="btn btn-success" onClick={()=>{
      
      setVal(val-1)
     Infor();
    
    }} >Previous</div> <div class="btn btn-success" onClick={()=>{
      
      setVal(val+1)
     Infor();
    
    }} >Next</div></div>
  </div>)
}

