import React, {useEffect,useState} from 'react'
import {Itemsele,ThemeWrap} from '../Home';
import item from '../imgs/item.jpg';


 

export function BidsHome(){
 let data=""
const[bids,setBids]=useState('') 
const[bidsNumb,setBidsNumb]=useState('') 
const[price,setPrice]=useState('') 
const[biddingMsg,setBiddingMsg]=useState('') 
const[biddingHeadline,setBiddingHeadline]=useState('') 

useEffect(()=>{
       fetch('/collection_bids_bids').then(res=>res.json()).then(res=>{
              setBidsNumb(res.length)
              res.forEach(bid=>{
             
                data+='<div>'+bid.contact+" amount: "+bid.amount+"</div><div>Trading ID: "+bid.tradingId+" ("+bid.traderName+")</div><hr></hr>"
              
              })
              
              setBids(data);
                })

                fetch('/collection_biddingControls').then(res=>res.json()).then(res=>{
setBiddingMsg(res[0].biddingMsg)
setBiddingHeadline(res[0].biddingHeadline)
setPrice(res[0].price)
                })

},[])
    


return(

  <div>
      <p></p>
  <div style={{fontSize:"24px",color:"red",textAlign:"center"}}>{biddingHeadline}</div><p></p>
  
    <p></p>
       <div style={{textAlign:"center",color:"grey",padding:"3px"}}>{biddingMsg}</div>
       <p></p>
       <ThemeWrap  img={item}date="Kayas Bidding" headline={price} msg="Keeping checking the website for changes in prices and as well notifications at the top of the page because we will keep updating you on issues concerning the winner of the offer. Good luck to the winner"/>
       <div style={{padding:"4px",fontSize:"20px",textAlign:"center",color:"red"}}><span style={{fontSize:"30px",fontWeight:"bold"}}>{bidsNumb} </span>students have posted their amounts. Scroll under the form below to see the posts:</div>
       <div style={{padding:"30px"}}>  
    <div style={{color:"green",fontWeight:"bold", fontSize:"24px"}}>How much can you offer? Post here:</div>
    
    <br></br><form method="post" action="/collection_bids_bid">
    <div class="mb-3">
  
    <input type="text" class="form-control" autoComplete="off" name="contact" placeholder='Your WhatsApp Contact e.g 0703852178' maxLength={10} minLength={10} required></input>
  <br></br>
  <div style={{fontFamily:"charm",color:"red"}}>Do not use commas as you enter your amount</div>
  <textarea type="text"  class="form-control" autoComplete="off" name="bidAmount" placeholder='Amount you would offer without commas e.g 10000 but not 10,000' maxLength={6} required></textarea>
  <br></br>
  
  <input type="text" class="form-control" autoComplete="off" name="tradingId" placeholder='Enter trading ID' maxLength={10} minLength={10} required></input>
  <div style={{fontFamily:"charm",color:"green",textAlign:""}}>Incase you don't know the trading ID and code, contact the student who sent this message to you.</div>
    <input type="text" class="form-control" autoComplete="off" name="tradingCode" placeholder='Enter trading code' maxLength={4} minLength={4} required></input>
    </div>
    <div style={{fontFamily:"charm",color:"red",textAlign:""}}>NOTE: The highest bidder will always appear on top</div>    
    <button style={{borderRadius:"18px"}} type="text" class="btn btn-success hovereffect"><span class="fa fa-money"></span> Post Amount</button>
    
    </form></div>

    
   
   
    <div style={{textAlign:"center",fontWeight:"bold",color:"red"}}>{bidsNumb} posts below:</div>
       <div style={{textAlign:"center",padding:"20px",fontSize:"15px",fontFamily:"charm"}}><div dangerouslySetInnerHTML={{__html:bids}}/></div>

       
       
      
 

  </div>
);
}



export function ViewOffer(){
  const[status,setStatus]=useState('')

 
return(

<div >
   


     <div class="row" style={{padding:"5px"}}>  
     <div class="col-md-3" style={{color:"green",textAlign:"center"}}><p></p> To view today's offer, enter your details below to continue</div>
    <div class="col-md-6"> <div style={{padding:"30px"}}>  

  
  <form method="post" action="/linkToOffer">
  <div class="mb-3">

  <input type="text" class="form-control" autoComplete="off"  name="contact" placeholder='Your WhatsApp Contact e.g 0703852178' maxLength={10} minLength={10} required></input>
<br></br>



  <input type="text" class="form-control" autoComplete="off" name="pin" placeholder='Enter Your PIN' maxLength={5} minLength={5} required></input>
  </div>
  
  <div class="row">

  <div class="col-6" ><div style={{color:"red"}}>{status}</div>
      <button onClick={

      ()=>{
             setStatus("Please wait ......... Incase it takes longer, then your  network connection may be poor")
      }
  } type="submit" style={{borderRadius:"18px"}} class="btn btn-success hovereffect"> Continue</button></div>
 <div class="col-6"  style={{padding:"6px"}}><a href="https://kayas-mak.herokuapp.com/pages/services" ><label style={{color:"green"}}><span class="hovereffect"><span class="fa fa-user"></span> REGISTER</span></label></a></div>
  
  
  </div>

  
  <div style={{fontSize:"12px",color:"green",textAlign:"left"}}>Thank you for Keeping It Kayas. Always Keep it Kayas</div>
  </form></div></div>
     
      </div>
    
<Itemsele />

</div>
);
}


export default BidsHome



